import React, { Component } from 'react';
import backImg from '../img/Component_2.png';
import img1 from '../img/Mask_Group_2.png';
import img2 from '../img/Mask_Group_3.png';
import img3 from '../img/Mask_Group_4.png';
import img4 from '../img/Mask_Group_5.png';
import img5 from '../img/Mask_Group_6.png';
import img6 from '../img/Mask_Group_7.png';
import img7 from '../img/Mask_Group_8.png';
import img8 from '../img/Mask_Group_9.png';


let elemA = null;
let elemB = null;
let frontA = "";
let backA = "";
let frontB = "";
let backB = "";
let canRotate = 0;
let move = 0;
let mo = 0;
let done = 0;
let didWin = "";
//import Card from './card';
let isNew = false;
class Game extends Component {
    state = { 
        cards:[
            { number: "n1", name:"11", img: img1 },
            { number: "n2", name:"22", img: img2 },
            { number: "n3", name:"33", img: img3 },
            { number: "n4", name:"44", img: img4 },

            { number: "n5", name:"55", img: img5 },
            { number: "n6", name:"66", img: img6 },
            { number: "n7", name:"77", img: img7 },
            { number: "n8", name:"88", img: img8 },

            { number: "n9", name:"88", img: img8 },
            { number: "n10", name:"77", img: img7 },
            { number: "n11", name:"66", img: img6 },
            { number: "n12", name:"55", img: img5 },

            { number: "n13", name:"44", img: img4 },
            { number: "n14", name:"33", img: img3 },
            { number: "n15", name:"22", img: img2 },
            { number: "n16", name:"11", img: img1 }
        ]
    }
    click = () => {
        console.log("click");
    }
    componentDidMount = () => {
        setTimeout(() => {
            
            this.state.cards.map((ccc)=>{
                document.getElementById(ccc.number+"-front").style.transform = "rotateY(0deg)";
                document.getElementById(ccc.number+"-back").style.transform = "rotateY(180deg)";
            })
        }, 500);
        setTimeout(() => {
            this.state.cards.map((ccc)=>{
                document.getElementById(ccc.number+"-front").style.transform = "rotateY(180deg)";
                document.getElementById(ccc.number+"-back").style.transform = "rotateY(0deg)";
            })
        }, 1700);
        isNew = false;

        
    }
    newGame = () => {
        isNew = true;
        //document.querySelector(".play").style.visibility= "hidden";
        document.querySelector(".play").style.display = "none";
        this.state.cards.map((ccc)=>{
            document.getElementById(ccc.number+"-front").style.transform = "rotateY(180deg)";
            document.getElementById(ccc.number+"-back").style.transform = "rotateY(0deg)";
        })
    }
    render() { 
        return ( 
            <div className="component">
                <h1>MEMORY CARD GAME</h1>
                <div className="game">
                    {
                        this.state.cards.map( card => <Card newGame={isNew} frontClass={card.number+"-front"} backClass={card.number+"-back"} key={card.number} onClick={this.click} name={card.name} number={card.number} frontImg={card.img} backImg={backImg} />)
                    }
                </div>
                <div className="footer">
                    <h3 className="new" onClick={this.newGame}>New game</h3>
                </div>
                <div className="play">
                    <h1>you {didWin}</h1>
                    
                    <h5 className="new" onClick={this.newGame}>New game</h5>
                </div>
            </div>
        );
    }
}



class Card extends Component {
    state = {  }
    doRotateA = () => {
        document.getElementById(frontA).style.transform = "rotateY(0deg)";
        document.getElementById(backA).style.transform = "rotateY(180deg)";
    }
    doRotateB = () => {
        document.getElementById(frontB).style.transform = "rotateY(0deg)";
        document.getElementById(backB).style.transform = "rotateY(180deg)";
    }
    dontRotateA = () => {
        document.getElementById(frontA).style.transform = "rotateY(180deg)";
        document.getElementById(backA).style.transform = "rotateY(0deg)";
    }
    dontRotateB = () => {
        document.getElementById(frontB).style.transform = "rotateY(180deg)";
        document.getElementById(backB).style.transform = "rotateY(0deg)";
    }
    flip = () =>{
        move++;

        if(elemA === null){
            elemA = this.props.name;
            frontA = this.props.frontClass;
            backA = this.props.backClass;
        }else {
            elemB = this.props.name;
            frontB = this.props.frontClass;
            backB = this.props.backClass;
        }

        if(canRotate === 2){}
        else if( canRotate ===1 ){
            this.doRotateB();
            canRotate++;
            setTimeout( () => {
                if(elemA === elemB){
                    done++;
                    console.log(done);
                }else {
                    this.dontRotateA();
                    this.dontRotateB();
                }
                elemA = null;
                elemB = null;
                frontA = "";
                backA = "";
                frontB = "";
                backB = "";
                canRotate = 0;

            },1000)
        }else {
            this.doRotateA();
            canRotate++;
            // move++;
        }

        setTimeout(() => {
            if(done == 8) {
                didWin = "win";
                mo=move;
                document.querySelector(".play").style.display = "block";
                done = 0;
            }
        }, 1000);
    }
    componentDidMount = () => {
        
        if (this.props.newGame){
            elemA = null;
            elemB = null;
            frontA = "";
            backA = "";
            frontB = "";
            backB = "";
            canRotate = 0;
            move = 0;
            done = 0;
        }
        
    }
    render() { 
        return ( 
            <div className="card" id="ccard" onClick={this.flip}>
                <img className="front-card"  id={this.props.frontClass}  src={this.props.frontImg} alt="" width="100%" height="100%"/>
                <img className="back-card"  id={this.props.backClass}  src={this.props.backImg} alt="" width="100%" height="100%"/>
            </div>
        );
    }
}
export default Game;